@use '../variables';
.infos-bar{
	padding: 16px 24px;
	background-color: white;
	display: flex;
	justify-content: space-between;
	align-items: center;

	&__infos{
		display: flex;
		gap: 40px;

		&__item{
			font-size: 14px;
			color:  variables.$textcolor;

			b {
				font-size: 18px;
				display: block;

			}
		}	
	}

	@media (max-width: 640px){
		display: block;

		&__infos{
			display: block;
			
			&__item{
				margin-bottom: 24px;
			}
		}
	}

	@media (max-width: 1024px){
		&__infos{
			gap: 16px;
		}
	}
}